'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';

interface DoctorSchedule {
  day: string;
  start_time: string;
  end_time: string;
}

export default function DoctorDashboard() {
  const router = useRouter();
  const [user, setUser] = useState<any>(null);
  const [schedule, setSchedule] = useState<DoctorSchedule[]>([]);
  const [appointments, setAppointments] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState('schedule');
  const [selectedDate, setSelectedDate] = useState('');

  useEffect(() => {
    const token = localStorage.getItem('token');
    const userRole = localStorage.getItem('userRole');
    const userStr = localStorage.getItem('user');

    if (!token || userRole !== 'doctor') {
      router.push('/doctor/login');
      return;
    }

    if (userStr) {
      const parsedUser = JSON.parse(userStr);
      setUser(parsedUser);
      fetchDoctorData(parsedUser.id);
    }
  }, []);

  const fetchDoctorData = async (doctorId: string) => {
    try {
      const today = new Date().toISOString().split('T')[0];
      const response = await fetch(
        `/api/doctors/${doctorId}?date=${today}`
      );
      const data = await response.json();
      setSchedule(data.schedule || []);
      setAppointments(data.appointments || []);
      setSelectedDate(today);
    } catch (error) {
      console.error('Error fetching doctor data:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    localStorage.removeItem('userRole');
    router.push('/');
  };

  const handleDateChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const date = e.target.value;
    setSelectedDate(date);

    try {
      const response = await fetch(
        `/api/doctors/${user.id}?date=${date}`
      );
      const data = await response.json();
      setAppointments(data.appointments || []);
    } catch (error) {
      console.error('Error fetching appointments:', error);
    }
  };

  if (!user) return <div className="text-center py-12">Loading...</div>;

  return (
    <div className="max-w-7xl mx-auto px-4 py-12">
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-4xl font-bold">Doctor Dashboard</h1>
        <button
          onClick={handleLogout}
          className="bg-red-600 text-white px-4 py-2 rounded hover:bg-red-700"
        >
          Logout
        </button>
      </div>

      {/* Profile Card */}
      <div className="bg-white rounded-lg shadow p-6 mb-8">
        <h2 className="text-2xl font-bold mb-4">Welcome, Dr. {user.full_name}!</h2>
        <p className="text-gray-600 mb-2">
          <strong>Email:</strong> {user.email}
        </p>
        <p className="text-gray-600">
          <strong>Specializations:</strong>{' '}
          {user.specializations?.join(', ') || 'General Practice'}
        </p>
      </div>

      {/* Tabs */}
      <div className="mb-6 flex space-x-4">
        <button
          onClick={() => setActiveTab('schedule')}
          className={`px-4 py-2 rounded ${
            activeTab === 'schedule'
              ? 'bg-blue-600 text-white'
              : 'bg-gray-200'
          }`}
        >
          Today's Schedule
        </button>
        <button
          onClick={() => setActiveTab('manage')}
          className={`px-4 py-2 rounded ${
            activeTab === 'manage'
              ? 'bg-blue-600 text-white'
              : 'bg-gray-200'
          }`}
        >
          Manage Schedule
        </button>
      </div>

      {/* Schedule Tab */}
      {activeTab === 'schedule' && (
        <div>
          <div className="bg-white rounded-lg shadow p-6 mb-6">
            <label className="block text-gray-700 font-bold mb-2">
              Select Date
            </label>
            <input
              type="date"
              value={selectedDate}
              onChange={handleDateChange}
              className="px-4 py-2 border border-gray-300 rounded"
            />
          </div>

          {appointments.length === 0 ? (
            <div className="bg-white rounded-lg shadow p-6 text-center">
              <p className="text-gray-600">No appointments for this date</p>
            </div>
          ) : (
            <div className="space-y-4">
              {appointments.map((appointment) => (
                <div key={appointment._id} className="bg-white rounded-lg shadow p-6">
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div>
                      <p className="font-bold">
                        {appointment.patient_id?.full_name}
                      </p>
                      <p className="text-gray-600 text-sm">
                        Patient ID: {appointment.patient_id?._id}
                      </p>
                    </div>
                    <div>
                      <p className="font-bold">
                        {appointment.clinic_id?.name}
                      </p>
                      <p className="text-gray-600 text-sm">
                        Room {appointment.room_id?.room_number}
                      </p>
                    </div>
                    <div>
                      <p className="text-sm">
                        <strong>Status:</strong>{' '}
                        <span className="capitalize text-blue-600">
                          {appointment.status}
                        </span>
                      </p>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      )}

      {/* Manage Schedule Tab */}
      {activeTab === 'manage' && (
        <div className="bg-white rounded-lg shadow p-6">
          <h2 className="text-2xl font-bold mb-4">Manage Working Schedule</h2>
          <p className="text-gray-600 mb-4">
            Current Schedule:
          </p>
          {schedule.length > 0 ? (
            <div className="space-y-2">
              {schedule.map((day, idx) => (
                <div key={idx} className="border border-gray-200 p-3 rounded">
                  <p className="font-bold">{day.day}</p>
                  <p className="text-gray-600">
                    {day.start_time} - {day.end_time}
                  </p>
                </div>
              ))}
            </div>
          ) : (
            <p className="text-gray-600">No schedule set</p>
          )}
          <p className="text-gray-600 mt-4">Schedule management coming soon...</p>
        </div>
      )}
    </div>
  );
}
